@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Farsan&family=Outfit:wght@200;300;400;500;600;700;800;900&family=Roboto&family=Yantramanav&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
}

body{
    background-color: #1B1F22;
}

h1, h4, p, a{
    color: #ffffff;
    text-decoration: none;
}

ul{
    list-style-type: none;
}

.btn{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background: gold;
    color: #000000;
    border: 1px solid rgb(255, 255, 255);
    font-weight: 600;
    cursor: pointer;
}

.btn-light{
    background: transparent;
    color: white;

}

.btn:hover{
    background: rgba(255, 255, 255, 0.2);
    color: white;
    transition: 0.3s ease-in-out;
}