.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.footer-container{
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 120px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
    align-items: center;

}

.location p{
    line-height: 30px;
    font-size: 1.5rem;
    
}

.phone{
    display: flex;
    margin-bottom: 0.8rem;
    align-items: center;
}

.phone p{
    line-height: 30px;
    font-size: 1.5rem;
}

.email{
    display: flex;
    margin-bottom: 0.8rem;
    align-items: center;
}

.email p{
    line-height: 30px;
    font-size: 1.5rem;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 2rem;
}


h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font: size 1.2em;
}

@media screen and (max-width: 640px){
    .footer-container{
        grid-template-columns: 1fr;
    }
}
    
