.aboutme {
    margin: 0;
    padding: 0;
    
  }
  
  #introduction {
    text-align: center;
    margin-bottom: 30px;
  }
  
  #introduction h2 {
    color: #beb9b9;
    font-size: 24px;
  }
  
  #introduction .content {
    margin: 20px;
  }
  
  #introduction p {
    line-height: 1.6;
  }
  
  #introduction .highlight {
    color: #ff40a9;
  }

  #introduction .titleintro{
    font-size: 1.2rem;
  }

  #introduction .details{
    text-align: left;
    margin-left: 60px;
    margin-top: 20px;
  }

  #introduction img{
    max-width: 100%;
    max-height: 100%;
  }
  
  .titlerow{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 0;
  }

  #education .marker {
    width: 20px;
    height: 20px;
    background-color: #FF4081;
    border-radius: 50%;
    margin-right: 20px;
    animation: pulse 1s infinite alternate;
  }
  
  #academic-experience,
  #professional-experience {
    text-align: center;
    padding-top: 20px;
  }
  
  #academic-experience h2,
  #professional-experience h2 {
    color: #cdc6c6;
    font-size: 26px;
    margin-top: 5px;
  }
  
  #skills {
    text-align: center;
    margin-bottom: 50px;
    padding-top: 30px;
  }
  
  #skills h2 {
    color: #c2bfbf;
    font-size: 24px;
  }
  
  #skills .skill-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
  }
  
  #skills .skill-list li {
    background-color: #FF4081;
    color: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    margin: 10px;
  }
  
  #contact {
    text-align: center;
    margin-bottom: 50px;
  }
  
  #contact h2 {
    color: #cbc4c4;
    font-size: 24px;
  }
  
  #contact .content {
    margin: 20px;
  }
  
  #contact p {
    line-height: 1.6;
  }
  
  #contact a {
    color: #c96186;
    text-decoration: none;
  }
  
  #contact a:hover {
    text-decoration: underline;
  }
  
  .row{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background: #1a1a1a;

  }
  .timeline {
    position: relative;
    margin: 20px auto;
    max-width: 600px;
    
    
  }
  
  .timeline-item {
    position: relative;
    margin-bottom: 40px;
  }
  
  .timeline-date {
    position: absolute;
    top: 0;
    left: -90px;
    font-weight: bold;
    color: #888;
  }
  
  .timeline-content {
    margin-left: 10px;
  }
  
  .timeline-content p {
    margin: 30px 0;
    text-align: center;
    font-size: 1rem;
  }
  
  .timeline-marker {
    position: absolute;
    left: -10px;
    top: 50%;
    transform: translateY(-50%);
    width: 10px;
    height: 10px;
    background-color: #888;
    border-radius: 50%;
  }

@media screen and (max-width: 1200px){
    #introduction img{
        display: none;
    }
    .titlerow{
        grid-template-columns: repeat(1, 1fr);
    }
}
    
