.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 100%;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding: 2rem 5rem;
    
    
}

.project-card{
    background: #c6bfc9;
    padding: 1.2rem 1rem;
    border-radius: 20px;
}

.project-card img{
    width: 100%;
    border-radius: 20px;
    
}

.project-title{
    padding: 1.2rem 0;
    color: rgb(255, 254, 254);
    border: 1px solid rgb(23, 22, 22);
    border-radius: 20px;
    text-align: center;
    background: #232224;
    margin-top: 1rem;
}


.project-description p{
    color: rgb(23, 22, 22); 
    font-size: 1.1rem;
    padding-bottom:1rem ;
    padding-top: 1rem;
    text-align: justify;
}
@media screen and (max-width: 768px){
    .project-container{
        max-width: 100%;
        margin: 0;
        grid-template-columns: 1fr;
    }
}
    