.hero2{
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;
}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero2::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1518770660439-4636190af475?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1170&q=80");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

}

.hero2 h1{
    font-size: 2.4rem;
}

.hero2 p{
    font-size: 1.4rem;
    text-align: center;
    margin-top: 1rem;
}

@media screen and (max-width: 640px){
    .hero2 h1{
        font-size: 1.8rem;
    }
}
    
